import React from 'react'
import "./section5.css";
import Carousel, { Dots } from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';
import { slidesToShowPlugin } from '@brainhubeu/react-carousel';
import {useHistory} from "react-router-dom";
function Section5() {
    const history = useHistory();
    function handleBeginnersClick()
    {
        history.push("/contact");
    }
    function handleIntemidiateClick()
    {
        history.push("/contact");
    }
    function handleAdvancedClick()
    {
        history.push("/contact");
    }
    return (
        <div className = "french-section-4">
        <h3 className = "french-section-4-heading">Our Popular <span style = {{color: "#2D3092"}}>Vedic Maths</span> Courses</h3>
        <div className = "carausel-container">
        <Carousel
            plugins={[
                'centered',
                'infinite',
                {
                resolve: slidesToShowPlugin,
                options: {
                numberOfSlides: 3
                }
                },
            ]}
            breakpoints={{
            480: {
                plugins: [
                'infinite',
                'arrows',
                'centered',
                {
                resolve: slidesToShowPlugin,
                options: {
                numberOfSlides: 1
                }
                },
            ]
            },
            700: {
                    plugins: [
                    'infinite',
                    'arrows',
                    'centered',
                    {
                    resolve: slidesToShowPlugin,
                    options: {
                    numberOfSlides: 1.5
                }
                },
            ]
            },
            900: {
                plugins: [
                'infinite',
                'arrows',
                'centered',
                {
                resolve: slidesToShowPlugin,
                options: {
                    numberOfSlides: 2
                }
                },
            ]
            },
            1200: {
                plugins: [
                'infinite',
                'centered',
                {
                resolve: slidesToShowPlugin,
                options: {
                    numberOfSlides: 3
                }
                },
            ]
            },
        }}>
        {/* Intemidiate Click */}
        <div className = "french-section-4-card" onClick = {handleIntemidiateClick}>
        <div className = "french-section-4-card-part-1">
                    <h4 >Classes 9-10</h4>
                    <h3>Intermediate</h3>
                    <h5>Curriculum consists of: </h5>
                    <div className = "french-section-4-card-part-1-points">
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>Live online classes</p>
                        </div>
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>Useful Course Materials</p>
                        </div>
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>Interactive Quizzes</p>
                        </div>
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>32 Classes during 4 months</p>
                        </div>
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>Completion Certificate </p>
                        </div>
                    </div>
                </div>
            <div className = "french-section-4-card-part-2">
                <div className = "french-section-4-card-part-2-price-container">
                    <div className = "discounted-price">
                            {/* <img src = "/rupeeSymbol.svg"/> */}
                            <p>&#8377;4,449</p>
                        </div>
                        <div className = "real-price">
                            <p>	&#xA0; &#8377; 6,299 &#xA0;</p>
                        </div>
                        <div className = "discount-percent">
                            29% off
                        </div>
                </div>
                <div className = "french-section-4-card-part-2-info-container">
                    <p className = "info">25 Classes, Rs 180 per session</p>
                </div>
            </div>
            <div className = "french-section-4-card-part-2-btn-container">
                <button className = "btn">Join Now</button>
            </div>
        </div>
        {/* Advanced Card */}
      <div className = "french-section-4-card" onClick = {handleAdvancedClick}>
            <div className = "french-section-4-card-part-1">
                <h4 >Classes 11-12+</h4>
                <h3>Advanced</h3>
                <h5>Curriculum consists of: </h5>
                <div className = "french-section-4-card-part-1-points">
                <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>Live online classes</p>
                        </div>
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>Useful Course Materials</p>
                        </div>
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>Focus on skill development</p>
                        </div>
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>48 Classes during 6 months</p>
                        </div>
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>In depth teaching of core topics</p>
                        </div>
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>Completion Certificate </p>
                        </div>
                </div>
            </div>
            <div className = "french-section-4-card-part-2">
                <div className = "french-section-4-card-part-2-price-container">
                    <div className = "discounted-price">
                        {/* <img src = "/rupeeSymbol.svg"/> */}
                        <p>&#8377;7,999</p>
                    </div>
                    <div className = "real-price">
                        <p>&#8377; 12,599</p>
                    </div>
                    <div className = "discount-percent">
                        36% off
                    </div>
                </div>
                <div className = "french-section-4-card-part-2-info-container">
                    <p className = "info">3 Classes, Rs 16 per session</p>
                </div>
            </div>
            <div className = "french-section-4-card-part-2-btn-container">
                <button className = "btn">Join Now</button>
            </div>
      </div>
      {/* Basic workshop Card */}
      <div className = "french-section-4-card" onClick = {handleBeginnersClick}>
            <div className = "french-section-4-card-part-1">
                <h4 >Classes 6-8</h4>
                <h3>Basic Workshop</h3>
                <h5>Curriculum consists of: </h5>
                <div className = "french-section-4-card-part-1-points">
                <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>Live online classes</p>
                        </div>
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>Useful Course Materials</p>
                        </div>
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>16 Classes during 2 months</p>                        
                        </div>
                        <div className = "french-section-4-card-part-1-point">
                            <img src = "/checkedBox.svg"/>
                            <p>Completion Certificate</p>
                        </div>
                </div>
            </div>
            <div className = "french-section-4-card-part-2">
                <div className = "french-section-4-card-part-2-price-container">
                    <div className = "discounted-price">
                        {/* <img src = "/rupeeSymbol.svg"/> */}
                        <p>&#8377;49</p>
                    </div>
                    <div className = "real-price">
                        <p>&#8377; 399</p>
                    </div>
                    <div className = "discount-percent">
                        87% off
                    </div>
                </div>
                <div className = "french-section-4-card-part-2-info-container">
                    <p className = "info">3 Classes, Rs 16 per session</p>
                </div>
            </div>
            <div className = "french-section-4-card-part-2-btn-container">
                <button className = "btn">Join Now</button>
            </div>
      </div>
    </Carousel>

        </div>
    </div>
    )
}

export default Section5
